@import "foundation/settings";
@import "foundation/components/grid";

.text-center {
    text-align: center;
}
.text-left {
    text-align: left;
}
.text-right {
    text-align: right;
}

* {
    font-family: "Proxima Nova", Helvetica, Arial, sans-serif;
}

section.last {
    padding-bottom: 3rem;
}

h1 {
    text-align: center;
    margin: 1rem 0 5rem;
}

h2 {
    border-top: 1px solid #bbb;
    padding-top: 1.5rem;
    margin-top: 3rem;
}

header {
    margin-bottom: 0;

    .project-facts {
        margin: 0 0 2rem;
    }

    .videoWrapper {
        position: relative;
        padding-bottom: 56.25%; /* 16:9 */
        padding-top: 25px;
        height: 0;
    }
    .videoWrapper iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    h3 {
        margin: .5rem 0;
    }
    p {
        margin: .5rem 0;
    }
    img {
        display: block;
        margin: 4rem auto;
    }
}

.contributions {
    background-color: #eee;
    border-radius: 4px;
    padding: 1rem 2rem;
    margin-top: 3rem;

    h3 {
        margin-top: 0;
    }
    p {
        margin-bottom: 0;
    }
}

img {
    display: block;
    margin: 3rem 0;
}